<template>
  <v-card
    height="100%"
  >
    <div
      id="gestion-docentes-categoria-instituto"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Categoría por Instituto.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('gestion-docentes-categoria-instituto')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as docentes.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="ColumnChart"
          />
        </v-responsive>
        <v-responsive style="width: 100%;">
          <v-simple-table
            dense
            class="cellheading mt-2"
          >
            <thead>
              <tr class="caption">
                <th>
                  Categoría
                </th>
                <th>
                  ICI
                </th>
                <th>
                  IDH
                </th>
                <th>
                  IDEI
                </th>
                <th>
                  ICO
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Profesores (A+B+C+Prof. Titular+Prof. Asociado+Prof. Adjunto)</td>
                <td>{{ categoriaInstitutosData.ICI.prof }}</td>
                <td>{{ categoriaInstitutosData.IDH.prof }}</td>
                <td>{{ categoriaInstitutosData.IDEI.prof }}</td>
                <td>{{ categoriaInstitutosData.ICO.prof }}</td>
              </tr>
              <tr>
                <td>D1+JTP</td>
                <td>{{ categoriaInstitutosData.ICI.jtp }}</td>
                <td>{{ categoriaInstitutosData.IDH.jtp }}</td>
                <td>{{ categoriaInstitutosData.IDEI.jtp }}</td>
                <td>{{ categoriaInstitutosData.ICO.jtp }}</td>
              </tr>
              <tr>
                <td>D2+Ayudante de 1ra</td>
                <td>{{ categoriaInstitutosData.ICI.ayu }}</td>
                <td>{{ categoriaInstitutosData.IDH.ayu }}</td>
                <td>{{ categoriaInstitutosData.IDEI.ayu }}</td>
                <td>{{ categoriaInstitutosData.ICO.ayu }}</td>
              </tr>
              <tr class="font-weight-bold">
                <td>Total</td>
                <td>{{ categoriaInstitutosData.ICI.total }}</td>
                <td>{{ categoriaInstitutosData.IDH.total }}</td>
                <td>{{ categoriaInstitutosData.IDEI.total }}</td>
                <td>{{ categoriaInstitutosData.ICO.total }}</td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-responsive>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['docentes', 'mobileSize']),
    barChartData () {
      return {
        cols: [
          { id: 'Categoría por Instituto', label: 'Categoría por Instituto', type: 'string' },
          { type: 'string', role: 'tooltip', p: { html: true } },
          { id: 'Profesores (A+B+C+Prof. Titular+Prof. Asociado+Prof. Adjunto)', label: 'Profesores (A+B+C+Prof. Titular+Prof. Asociado+Prof. Adjunto)', type: 'number' },
          { role: 'annotation' },
          { id: 'D1+JTP', label: 'D1+JTP', type: 'number' },
          { role: 'annotation' },
          { id: 'D2+Ayudante de 1ra', label: 'D2+Ayudante de 1ra', type: 'number' },
          { role: 'annotation' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'ICI' },
              { v: 0, f: this.createTooltip('ICI', this.categoriaInstitutosData.ICI) },
              {
                v: this.categoriaInstitutosData.ICI.prof,
                f: this.categoriaInstitutosData.ICI.prof
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.ICI.prof)}` },
              {
                v: this.categoriaInstitutosData.ICI.jtp,
                f: this.categoriaInstitutosData.ICI.jtp
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.ICI.jtp)}` },
              {
                v: this.categoriaInstitutosData.ICI.ayu,
                f: this.categoriaInstitutosData.ICI.ayu
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.ICI.ayu)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'IDH' },
              { v: 0, f: this.createTooltip('IDH', this.categoriaInstitutosData.IDH) },
              {
                v: this.categoriaInstitutosData.IDH.prof,
                f: this.categoriaInstitutosData.IDH.prof
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.IDH.prof)}` },
              {
                v: this.categoriaInstitutosData.IDH.jtp,
                f: this.categoriaInstitutosData.IDH.jtp
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.IDH.jtp)}` },
              {
                v: this.categoriaInstitutosData.IDH.ayu,
                f: this.categoriaInstitutosData.IDH.ayu
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.IDH.ayu)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'IDEI' },
              { v: 0, f: this.createTooltip('IDEI', this.categoriaInstitutosData.IDEI) },
              {
                v: this.categoriaInstitutosData.IDEI.prof,
                f: this.categoriaInstitutosData.IDEI.prof
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.IDEI.prof)}` },
              {
                v: this.categoriaInstitutosData.IDEI.jtp,
                f: this.categoriaInstitutosData.IDEI.jtp
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.IDEI.jtp)}` },
              {
                v: this.categoriaInstitutosData.IDEI.ayu,
                f: this.categoriaInstitutosData.IDEI.ayu
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.IDEI.ayu)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'ICO' },
              { v: 0, f: this.createTooltip('ICO', this.categoriaInstitutosData.ICO) },
              {
                v: this.categoriaInstitutosData.ICO.prof,
                f: this.categoriaInstitutosData.ICO.prof
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.ICO.prof)}` },
              {
                v: this.categoriaInstitutosData.ICO.jtp,
                f: this.categoriaInstitutosData.ICO.jtp
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.ICO.jtp)}` },
              {
                v: this.categoriaInstitutosData.ICO.ayu,
                f: this.categoriaInstitutosData.ICO.ayu
              },
              { v: 0, f: `${this.hiddeAnnotation(this.categoriaInstitutosData.ICO.ayu)}` }
            ]
          }
        ]
      }
    },
    barChartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 40,
          top: 40,
          bottom: 20
        },
        // colors: state.colores.investigadoresSexo,
        focusTarget: 'category',
        tooltip: {
          isHtml: true
          // textStyle: { color: 'white', bold: false }
        },
        colors: this.colors,
        // title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        isStacked: true,
        legend: {
          position: 'top',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '40%' },
        annotations: {
          // style: 'line'
          highContrast: false,
          textStyle: {
            color: 'white',
            //   opacity: 0.9,
            fontSize: 13
          }
        },
        hAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
            // fontSize: 14
          },
          slantedText: false
        },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    categoriaInstitutosData () {
      return this.docentes
        .reduce((acc, cur) => {
          const nivA = new RegExp('Niv A', 'gm')
          const nivB = new RegExp('Niv B', 'gm')
          const nivC = new RegExp('Niv C', 'gm')
          const nivD1 = new RegExp('Niv D1', 'gm')
          const nivD2 = new RegExp('Niv D2', 'gm')
          const profesores = nivA.test(cur.descripcion_categoria) ||
          nivB.test(cur.descripcion_categoria) ||
          nivC.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Profesor Asociado' ||
          cur.descripcion_categoria === 'ASOCIADO SIMPLE' ||
          cur.descripcion_categoria === 'Profesor Titular' ||
          cur.descripcion_categoria === 'Profesor Adjunto' ||
          cur.descripcion_categoria === 'ADJUNTO SIMPLE'
          const d1jtp = nivD1.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Jefe Trab. Practicos' ||
          cur.descripcion_categoria === 'Jef Trab Pract NM' ||
          cur.descripcion_categoria === 'JTP SIMPLE'
          const d2ayu = nivD2.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Ayudante de 1ra.' ||
          cur.descripcion_categoria === 'AYUDANTE DE 1º SIMPL'
          const ICI = cur.unidad_academica === 'ICI'
          const IDH = cur.unidad_academica === 'IDH'
          const IDEI = cur.unidad_academica === 'IDEI'
          const ICO = cur.unidad_academica === 'ICO'
          return ICI
            ? profesores
              ? { ...acc, ICI: { ...acc.ICI, prof: acc.ICI.prof + 1, total: acc.ICI.total + 1 } }
              : d1jtp
                ? { ...acc, ICI: { ...acc.ICI, jtp: acc.ICI.jtp + 1, total: acc.ICI.total + 1 } }
                : d2ayu
                  ? { ...acc, ICI: { ...acc.ICI, ayu: acc.ICI.ayu + 1, total: acc.ICI.total + 1 } }
                  : acc
            : IDH
              ? profesores
                ? { ...acc, IDH: { ...acc.IDH, prof: acc.IDH.prof + 1, total: acc.IDH.total + 1 } }
                : d1jtp
                  ? { ...acc, IDH: { ...acc.IDH, jtp: acc.IDH.jtp + 1, total: acc.IDH.total + 1 } }
                  : d2ayu
                    ? { ...acc, IDH: { ...acc.IDH, ayu: acc.IDH.ayu + 1, total: acc.IDH.total + 1 } }
                    : acc
              : IDEI
                ? profesores
                  ? { ...acc, IDEI: { ...acc.IDEI, prof: acc.IDEI.prof + 1, total: acc.IDEI.total + 1 } }
                  : d1jtp
                    ? { ...acc, IDEI: { ...acc.IDEI, jtp: acc.IDEI.jtp + 1, total: acc.IDEI.total + 1 } }
                    : d2ayu
                      ? { ...acc, IDEI: { ...acc.IDEI, ayu: acc.IDEI.ayu + 1, total: acc.IDEI.total + 1 } }
                      : acc
                : ICO
                  ? profesores
                    ? { ...acc, ICO: { ...acc.ICO, prof: acc.ICO.prof + 1, total: acc.ICO.total + 1 } }
                    : d1jtp
                      ? { ...acc, ICO: { ...acc.ICO, jtp: acc.ICO.jtp + 1, total: acc.ICO.total + 1 } }
                      : d2ayu
                        ? { ...acc, ICO: { ...acc.ICO, ayu: acc.ICO.ayu + 1, total: acc.ICO.total + 1 } }
                        : acc
                  : acc
        },
        {
          ICI: {
            prof: 0,
            jtp: 0,
            ayu: 0,
            total: 0
          },
          IDH: {
            prof: 0,
            jtp: 0,
            ayu: 0,
            total: 0
          },
          IDEI: {
            prof: 0,
            jtp: 0,
            ayu: 0,
            total: 0
          },
          ICO: {
            prof: 0,
            jtp: 0,
            ayu: 0,
            total: 0
          }
        })
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },

    createTooltip (title, value) {
      return `<div style="min-width: 370px; padding: 10px 10px 10px 10px; font-size: 13px; color: black;">
      <h3>${title}</h3>
      <ul>
      <li>Profesores (A+B+C+Prof. Titular+Prof. <br>Asociado+Prof. Adjunto): ${value.prof}</li>
      <li>D1+JTP: ${value.jtp}</li>
      <li>D2+Ayudante de 1ra: ${value.ayu}</li>
      <li>Total: ${value.total}</li>
      </li>
      </ul></div>`
    },
    hiddeAnnotation (v) {
      return v <= 6 ? '' : `${v}`
    }
  }
}
</script>
